<template> 
 <div>


    <button id="cargarCampo" @click="cargarCampo">
        cargar campo
    </button>

    <form id="astro1"  action="" v-on:submit.prevent="cargarInput1()" method="POST" style="display: inline-block;">

        <input list="opcionesJupiter1" type="text" v-model="dato1" name="nombre" id="imput1" placeholder="__jpiter en signo">
        <datalist id="opcionesJupiter1">
          <option v-for="signo in signosJupiter" :key="signo.id">{{signo}}</option> 
        </datalist>



        <button  id="cargar1" type="submit">
            cargar
        </button>
        <button  id="borrar1" @click.prevent="deleteInput1()">
            borrar
        </button>
    </form>
    <form id="astro2" v-on:submit.prevent="cargarInput2()" action="" method="POST" style="display: inline-block; " >

        <input list="opcionesJupiter2" type="text" v-model="dato2" name="nombre" id="imput2" placeholder="__jpiter en casa">
        <datalist id="opcionesJupiter2">
            <option v-for="casa in casasJupiter" :key="casa.id">{{casa}}</option>
        </datalist>






        <button  id="cargar2" type="submit">
            cargar
        </button>
        <button  id="borrar2" @click.prevent="deleteInput2()">
            borrar
        </button>
        
    </form>
    
    <div >
        <div style="float: left;">
            <button id='limpiar' v-on:click="limpiarCampo">
                limpiar campo
            </button>
        </div>
        <form id="aspectos"  v-on:submit.prevent="cargarInput3()" action="" style="position:relative; left: 200px; margin-top: 10px;" >
        
        <div id="imput3"  style="float:left">
            <div v-for="(find,index) in dato3" :key="find.id">
                <input list="opcionesJupiter" v-model="find.value" :key="index" placeholder="__aspectos del jpiter">
                <datalist id="opcionesJupiter">
                    <option v-for="aspecto in aspectos" :key="aspecto.id">{{aspecto}}</option>
                </datalist>



            </div>
        </div>
        
        <div style="position: absolute; left:280px">
            <button  id="agregar3" @click.prevent= "addAspecto">
                agregar
            </button>
            <button  id="borrar3" @click.prevent="deleteAspecto">
                borrar
            </button>
            <div>
                <button  id="enviaraspectos" type="submit">
                    cargar aspectos
                </button>
                <div style="clear: both;">

                </div>
            </div>
            
        </div>
        
        </form>
    </div>
    
    
</div>
</template>



<script>



export default {
  name: 'jupiter',
  methods:{
    


    cargarCampo(){
        this.cargarInput1();
        this.cargarInput2();
        this.cargarInput3();
    }
    ,
    async cargarInput1(){
        
        if(this.dato1!=""){
        let peticionTipo={nombre:'jupiter_ensigno'};
        await axios.post('/carta',peticionTipo).then((response)=>{
            let texto={
                'id':16,
                'nombre':'jupiter en el signo',
                'definicion': response.data
            }
            
            this.$emit('tipo1',texto);
           })
        let peticion={
            nombre:this.dato1
        }
         await axios.post('/carta',peticion).then((response)=>{
        let texto={
            'id':16,
            'nombre':this.dato1,
            'definicion': response.data
        }
        
        this.$emit('first',texto);
       })}
    
    },
    async cargarInput2(){
        if(this.dato2!=""){
        let peticionTipo={nombre:'jupiter_encasa'};
        await axios.post('/carta',peticionTipo).then((response)=>{
            let texto={
                'id':16,
                'nombre':'jupiter en la Casa',
                'definicion': response.data
            }
            
            this.$emit('tipo2',texto);
           })
        let peticion={
            nombre:this.dato2
        }
         await axios.post('/carta',peticion).then((response)=>{
        let texto={
            'id':16,
            'nombre':this.dato2,
            'definicion': response.data
        }
        
        this.$emit('second',texto);
       })}
    
    },
    async cargarInput3(){
        
         let {value}=this.dato3[0];
        
       if (value!=''){
       
        await axios.post('/aspectos',this.dato3).then((response)=>{        
        let nombre=response.data.value;
        let definicion=response.data.respuestas;
        let data=new Array(definicion.length);
        let i=0;
        nombre.forEach(element => {
           if(definicion[i]!=""){
            data[i]={
                'nombre':element,
                'definicion':definicion[i]
            }}else{data[i]={'nombre':'','definicion':''}
        }
            i++;
        });
        let texto={
            'id':16,
            'aspectos':data
        }
        this.$emit('third',texto);
        
      });
       }
    }
    
    ,
    addAspecto:function(){
        this.dato3.push({value:''})
        
    }
    ,
    deleteAspecto:function(index){
        console.log(index);
        console.log(this.dato3);
        let ind=this.dato3.length;
        this.dato3.splice(ind-1,1);
        let texto={'id':16}
        this.$emit('deleteAspecto',texto);
    }
    ,
    deleteInput1(){
        this.dato1="";
        let texto={'id':16}
        this.$emit('deleteDefinicion1',texto);
    }
    ,
    deleteInput2(){
        this.dato2="";
        let texto={'id':16}
        this.$emit('deleteDefinicion2',texto);
    },
    deleteAspectos(){
        this.dato3=[];
        let texto={'id':16}
        this.$emit('deleteAspectos',texto)
    }
    ,
    limpiarCampo(){
     this.deleteInput1();
     this.deleteInput2();   
     this.deleteAspectos();
     this.addAspecto();   
    }
     
    
},

data(){
    return{
        dato1:'',
        dato2:'',
        count:0,
        dato3:[],
        casasJupiter:[
          "jpiter en casa 1",
            "jpiter en casa 2",
            "jpiter en casa 3",
            "jpiter en casa 4",
            "jpiter en casa 5",
            "jpiter en casa 6",
            "jpiter en casa 7",
            "jpiter en casa 8",
            "jpiter en casa 9",
            "jpiter en casa 10",
            "jpiter en casa 11",
            "jpiter en casa 12"
        ],
      signosJupiter:[
         "jpiter en aries",
            "jpiter en tauro",
            "jpiter en geminis",
            "jpiter en cancer",
            "jpiter en leo",
            "jpiter en virgo",
            "jpiter en libra",
            "jpiter en escorpio",
            "jpiter en sagitario",
            "jpiter en capricornio",
            "jpiter en acuario",
            "jpiter en piscis"
      ],
       aspectos:[
         "jpiter conjuncin ",
                    "jpiter trgono ",
                    "jpiter sextil ",
                    "jpiter cuadratura ",
                    "jpiter oposicin "
       ]         
    }
}
,
mounted(){
    this.addAspecto()
},


  created: function(){
    this.$parent.$on('clean', this.limpiarCampo);
    
    
    this.$parent.$on('update', this.cargarCampo);
  }
}

import axios from 'axios'
  

</script>